import {
  ChangeDetectionStrategy,
  Component,
  OnDestroy,
  OnInit
} from '@angular/core';
import { NbSidebarService, NbThemeService } from '@nebular/theme';
import { map, takeUntil } from 'rxjs/operators';
import { Subject, Subscription } from 'rxjs';

import { AuthService } from '../auth/auth.service';

@Component({
  selector: 'app-layout',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit, OnDestroy {
  isAuth = false;
  authSubscription: Subscription;

  private destroy$: Subject<void> = new Subject<void>();

  themes = [
    { value: 'default', name: 'Light', },
    { value: 'dark', name: 'Dark', },
    { value: 'cosmic', name: 'Cosmic', },
    { value: 'corporate', name: 'Corporate', },
  ];

  items = [
    {
      title: 'Members',
      icon: 'people-outline',
      children: [
        {
          title: 'Add New Member',
          link: ['member/add-member'],
          icon: 'person-add-outline'
        },
        {
          title: 'View Members',
          link: ['member'], // goes into angular `routerLink`,
          icon: 'person-outline'
        },
        {
          title: 'Attendance',
          link: ['member/attendance'], // goes into angular `routerLink`,
          icon: 'calendar-outline'
        }
      ]
    },
    {
      title: 'General Settings',
      icon: 'settings-outline',
      children: [
        {
          title: 'Member Format',
          link: ['member/form-format'],
          icon: 'file-text-outline'
        },
        {
          title: 'Batches',
          link: ['general/batch-info'], // goes into angular `routerLink`,
          icon: 'clock-outline'
        },
        {
          title: 'Working Days',
          link: ['general/working-days'],
          icon: 'calendar-outline'
        }
      ]
    }
  ];

  currentTheme = 'default';

  constructor(
    private sidebarService: NbSidebarService,
    private themeService: NbThemeService,
    private authService: AuthService
  ) {
    this.themeService
      .onThemeChange()
      .pipe(
        map(({ name }) => name),
        takeUntil(this.destroy$)
      )
      .subscribe(themeName => (this.currentTheme = themeName));
  }

  ngOnInit() {
    this.authSubscription = this.authService.authChange.subscribe(
      authStatus => {
        this.isAuth = authStatus;
        console.log(this.isAuth);
      }
    );
  }

  onLogout() {
    this.authService.logout();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  changeTheme(themeName: string) {
    this.themeService.changeTheme(themeName);
  }

  toggle() {
    this.sidebarService.toggle(true, 'left');
  }
}
