import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import {
  FormGroup,
  FormControl,
  FormArray,
  Validators,
  FormBuilder,
  NgForm
} from '@angular/forms';

import { AngularFirestore } from '@angular/fire/firestore';
import { MemberService } from 'src/app/shared/member.service';
import { NbWindowService } from '@nebular/theme';

import { NbToastrService } from '@nebular/theme';

@Component({
  selector: 'app-form-format',
  templateUrl: './form-format.component.html',
  styleUrls: ['./form-format.component.scss']
})
export class FormFormatComponent implements OnInit {
  @ViewChild('contentTemplate', { static: false }) contentTemplate: TemplateRef<
    any
  >;

  loading = false;
  existFields: any = [];
  existFieldData: any = [];
  form: FormGroup;
  fieldOrder: number;

  constructor(
    private memberService: MemberService,
    private fb: FormBuilder,
    private firestore: AngularFirestore,
    private toastrService: NbToastrService,
    private windowService: NbWindowService
  ) {}

  showToast(position, status) {
    this.toastrService.show(status || 'Success', `Data Added Successfully!`, {
      position,
      status
    });
  }

  onEditField(form: NgForm) {
    this.firestore.doc('form-format/' + form.value.id).update(form.value);
    this.showToast('top-right', 'success');
  }

  editField(i: number) {
    this.existFieldData = this.existFields[i];
    this.windowService.open(this.contentTemplate, {
      title: 'EditField',
      context: {
        text: 'some text to pass into template'
      }
    });
  }

  formData() { 
    return (<FormArray>this.form.get('memberFields')).controls; 
  }

  ngOnInit() {
    this.memberService.getFormFormat().subscribe(actionArray => {
      this.existFields = actionArray.map(item => {
        return {
          id: item.payload.doc.id,
          ...item.payload.doc.data()
        };
      });
      this.fieldOrder = this.existFields.length;
    });

    this.form = this.fb.group({
      memberFields: this.fb.array([
        this.fb.group({
          fieldName: new FormControl(null, Validators.required),
          fieldType: new FormControl('text', Validators.required),
          fieldRequired: new FormControl(false),
          fieldDisabled: new FormControl(false)
        })
      ])
    });
  }

  addNewField() {
    const creds = this.form.controls.memberFields as FormArray;
    creds.push(
      this.fb.group({
        fieldName: new FormControl(null, Validators.required),
        fieldType: new FormControl('text', Validators.required),
        fieldRequired: new FormControl(false),
        fieldDisabled: new FormControl(false)
      })
    );
  }

  deleteField(index: number) {
    (<FormArray>this.form.get('memberFields')).removeAt(index);
  }

  onSubmit() {
    for (let i = 0; i < this.form.value.memberFields.length; i++) {
      this.form.value.memberFields[i].fieldOrder = this.fieldOrder;
      this.firestore
        .collection('form-format')
        .add(this.form.value.memberFields[i])
        .then(
          res => {
            this.showToast('top-right', 'success');
          },
          err => console.log(err)
        );
      this.fieldOrder++;
    }
  }
}
