import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common';
import {
  FormGroup,
  FormControl,
  FormArray,
  Validators,
  FormBuilder
} from '@angular/forms';

import { AngularFirestore } from '@angular/fire/firestore';
import { MemberService } from 'src/app/shared/member.service';
import { NbWindowService } from '@nebular/theme';

import { NbToastrService } from '@nebular/theme';

@Component({
  selector: 'app-attendance',
  templateUrl: './attendance.component.html',
  styleUrls: ['./attendance.component.scss']
})
export class AttendanceComponent implements OnInit {
  memberData;
  attendanceMemberData;
  form: FormGroup;
  date = new Date();
  batchValue: number = 0;
  batchData;
  joiningData;
  formDisplay = false;

  constructor(
    private memberService: MemberService,
    private fb: FormBuilder,
    private firestore: AngularFirestore,
    private toastrService: NbToastrService,
    private windowService: NbWindowService,
    private datePipe: DatePipe
  ) {}

  showToast(position, status, message) {
    this.toastrService.show(status || 'Success', message, {
      position,
      status
    });
  }

  batchChange(batch) {
    this.formData();
    this.formDisplay = true;
  }

  dateChange(date) {
    this.formData();
    this.formDisplay = true;
  }

  onSubmit() {
    let selectedDate = this.datePipe.transform(this.date, 'dd-MM-yyyy');
    for (let i = 0; i < this.form.value.attendanceField.length; i++) {
      this.firestore
        .collection('member-info')
        .doc(this.form.value.attendanceField[i].id)
        .collection('attendance-detail')
        .doc(selectedDate)
        .set({
          Date: this.date,
          DateString: selectedDate,
          attendance: this.form.value.attendanceField[i].attendance
        })
        .then(
          res => {
            this.formDisplay = false;
            this.showToast(
              'top-right',
              'success',
              'Attendance Added Successfully!'
            );
          },
          err => console.log(err)
        );
    }
  }

  formData1() { 
    return (<FormArray>this.form.get('attendanceField')).controls; 
  }

  ngOnInit() {
    this.form = this.fb.group({
      attendanceField: this.fb.array([])
    });

    this.memberService.getBatchInfo().subscribe(actionArray => {
      this.batchData = actionArray.map(item => {
        return {
          id: item.payload.doc.id,
          ...item.payload.doc.data()
        };
      });
    });
  }

  formData() {
    let selectedDate = this.datePipe.transform(this.date, 'dd-MM-yyyy');
    let creds = this.form.controls.attendanceField as FormArray;
    creds.controls = [];

    this.memberService.getMemberInfo().subscribe(actionArray => {
      this.memberData = actionArray.map(item => {
        return {
          id: item.payload.doc.id,
          ...item.payload.doc.data()
        };
      });

      for (let member of this.memberData) {
        this.memberService.getJoiningInfo(member.id).subscribe(actionArray => {
          this.joiningData = actionArray.map(item => {
            return {
              id: item.payload.doc.id,
              ...item.payload.doc.data()
            };
          });
          if (this.batchValue == this.joiningData[0].Batch) {
            this.memberService
              .getMemberAttendanceInfo(member.id, selectedDate)
              .subscribe(actionArray => {
                this.attendanceMemberData = actionArray.map(item => {
                  return {
                    id: item.payload.doc.id,
                    ...item.payload.doc.data()
                  };
                });

                if (this.attendanceMemberData.length > 0) {
                  const creds = this.form.controls.attendanceField as FormArray;
                  creds.push(
                    this.fb.group({
                      name: new FormControl(member.Name, Validators.required),
                      id: new FormControl(member.id, Validators.required),
                      attendance: new FormControl(
                        this.attendanceMemberData[0].attendance
                      )
                    })
                  );
                } else {
                  const creds = this.form.controls.attendanceField as FormArray;
                  creds.push(
                    this.fb.group({
                      name: new FormControl(member.Name, Validators.required),
                      id: new FormControl(member.id, Validators.required),
                      attendance: new FormControl(false)
                    })
                  );
                }
              });
          }
        });
      }
    });
  }
}
