import { NgModule } from '@angular/core';

import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { LoginComponent } from './auth/login/login.component';
import { SignupComponent } from './auth/signup/signup.component';
import { MemberComponent } from './member/member.component';
import { AuthGuard } from './auth/auth.guard';
import { FormFormatComponent } from './member/form-format/form-format.component';
import { BatchInfoComponent } from './general/batch-info/batch-info.component';
import { AddMemberComponent } from './member/add-member/add-member.component';
import { AttendanceComponent } from './member/attendance/attendance.component';
import { MemberInfoComponent } from './member/member-info/member-info.component';
import { WorkingDaysComponent } from './general/working-days/working-days.component';

const routes: Routes = [
  { path: '', redirectTo: '/member', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'signup', component: SignupComponent },
  { path: 'member', component: MemberComponent, canActivate: [AuthGuard] },
  {
    path: 'member/form-format',
    component: FormFormatComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'member/add-member',
    component: AddMemberComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'member/attendance',
    component: AttendanceComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'member/member-info/:memberId',
    component: MemberInfoComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'general/batch-info',
    component: BatchInfoComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'general/working-days',
    component: WorkingDaysComponent,
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule {}
