import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class MemberService {
  constructor(private firestore: AngularFirestore) {}

  getFormFormat() {
    return this.firestore
      .collection('form-format', ref => ref.orderBy('fieldOrder'))
      .snapshotChanges();
  }

  getBatchInfo() {
    return this.firestore
      .collection('batch-info', ref => ref.orderBy('fieldOrder'))
      .snapshotChanges();
  }

  getBatchNumber(fieldOrder) {
    return this.firestore
      .collection('batch-info/', ref =>
        ref.where('fieldOrder', '==', fieldOrder)
      )
      .snapshotChanges();
  }

  getGeneralInfo() {
    return this.firestore.collection('general-info').snapshotChanges();
  }

  getMemberInfo() {
    return this.firestore.collection('member-info').snapshotChanges();
  }

  getJoiningInfo(id) {
    return this.firestore
      .collection('member-info/' + id + '/joining-detail', ref =>
        ref.orderBy('StartingDate', 'desc')
      )
      .snapshotChanges();
  }

  getAccountInfo(id) {
    return this.firestore
      .collection('member-info/' + id + '/account-detail', ref =>
        ref.orderBy('Date', 'desc')
      )
      .snapshotChanges();
  }

  getMemberAttendanceInfo(id, date) {
    return this.firestore
      .collection('member-info/' + id + '/attendance-detail', ref =>
        ref.where('DateString', '==', date)
      )
      .snapshotChanges();
  }

  getMemberAttendanceHistory(id) {
    return this.firestore
      .collection('member-info/' + id + '/attendance-detail', ref =>
        ref.orderBy('Date', 'desc').limit(30)
      )
      .snapshotChanges();
  }

  getMemberConsultHistory(id) {
    return this.firestore
      .collection('member-info/' + id + '/consult-detail', ref =>
        ref.orderBy('Date', 'desc').limit(10)
      )
      .snapshotChanges();
  }
}
